<template>
  <div>
    <mt-tabbar v-model="selected">
      <mt-tab-item id="" v-for="item in tabs" :key="item.name" :class="{'active': selected === item.name}" @click.native="goTo(item)">
        <icon :name="item.icon" slot="icon"></icon>
        {{item.title}}
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>

export default {
  name: 'hello',
  props: {
    select: {
      type: String,
      default: 'scan',
    },
  },
  data() {
    return {
      selected: this.select,
      tabs: [
        {
          name: 'goods',
          title: '',
          url: '/goods',
          icon: 'shangpin',
        },
        {
          name: 'scan',
          title: '',
          url: '/goods',
          icon: 'saomaruku',
        },
        {
          name: 'journals',
          title: '',
          url: '/journals',
          icon: 'liushui',
        },
      ],
    };
  },
  methods: {
    goTo(item) {
      this.selected = item.name;
      if (item.name === 'scan') {
        const that = this;
        this.$wechat.scanQRCode({
          needResult: 1, // 01
          scanType: ['qrCode', 'barCode'], // 
          success: (res) => {
            const result = res.resultStr; // needResult  1 
            that.$router.push({ name: 'search', query: { barcode: result } });
          },
        });
        return;
      }
      this.$router.push({ path: item.url });
    },
  },
};
</script>

